<template>
<div class="home-wrap">
    <router-link to="/catalog"></router-link>
</div>
</template>
 
<script>
import carousel from '../components/carousel'
import axios from 'axios'
export default{
    props: [''],
    components: {
        carousel,
    },
    
    data() {
        return {
            username: '', 
            password: '',
            repassword: '',
            userInfo: '',
            isShowRegisterPage: false, //
            isShowLoginPage: true, //
            isSuccessLoginPage: false, //
        }
    },
    
    mounted(){
        // this.initData();
    },
    
    methods: {
        /**
         * 
         */
        toRegisterPage() {
            this.isShowLoginPage = false;
            this.isShowRegisterPage = true;
        },

        /**
         * 
         */
        toLoginPage() {
            this.isShowLoginPage = true;
            this.isShowRegisterPage = false;
        },

        /**
         * 
         */
        toLogin() {
            debugger
            axios
            .post("/api/api/user/login", {
                username: this.username,
                password: this.password
            })
            .then( (res) => {
                debugger
                if(res.data.code == 0) {
                    this.message = res.data.message;
                    this.userInfo = res.data.userInfo;
                    if(this.message == '') {
                        this.isSuccessLoginPage = true;
                        this.isShowLoginPage = false;
                    }
                    this.$set(res.data, 'userInfo', res.data.userInfo)
                }
            })
            .catch( (err) => {
                console.log(err)
            })
        },

        /**
         * 
         */
        toRegister() {
            axios
            .post("/api/api/user/register", {
                username: this.username,
                password: this.password,
                repassword: this.repassword
            })
            .then( (res) => {
                debugger
                if(res.data.code == 0) {
                    if(this.message == '') {
                        this.isShowRegisterPage = false;
                        this.isShowLoginPage = true;
                    }
                    this.userInfo = res.data.userInfo;
                    this.$set(res.data, 'userInfo', res.data.userInfo)
                }
            })
            .catch( (err) => {
                console.log(err)
            })
        },

        /**
         * 
         */
        loginOut() {
            // 
            axios
            .get("/api/api/user/logout", {})
            .then( (res) => {
                debugger
                if(!res.code){
                    window.location.reload();
                }
            })
            .catch( (err) => {
                console.log(err)
            })
        },

        /**
         * 
         */
        initData() {
            this.$http.get('/api/').then((response) => {
                console.log(response)
            })
        }
    },
    
    }
</script>
 
<style lang='less' scoped>
    .home-wrap{
        padding-top: 1rem;
        background: #fee;
    }
    .Box{
        width: 8rem;
        height: 8rem;
        background: #f5f5f5;
        margin: .625rem auto;
        padding-top: 0.2rem;
        .LRSBox{
            width: 7rem;
            height: 4rem;
            border: solid .0625rem #ccc;
            margin: 1rem auto;
            .title{
                height: 1rem;
                line-height: 1rem;
            }
            .inputBox{
                height: 0.6rem;
                line-height: 0.6rem;
                .colHeader{
                    width: 1.2rem;
                    display: inline-block;
                    text-align: right;
                }
            }
        }
    }
</style>



